import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FaqComponent } from './faq.component';
import { MaterialModule } from '@jgpl/shared';
import { RouterModule } from '@angular/router';
import { WebfooterModule } from '@jgpl/shared/webfooter/webfooter.module';
import { WebheaderModule } from '@jgpl/shared/webheader/webheader.module';



@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    MaterialModule,
    WebheaderModule,
    WebfooterModule,
    RouterModule.forChild([
      { path: '', component: FaqComponent  }
    ])
  ]
})
export class FaqModule { }
